* {
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
}

.container {
  max-width: 1220px;
  margin: auto;
}

.header {
  padding-top: 43px;
}

.header {
  background: url("../img/hero-bg.png") 85% 100% no-repeat,
    url("../img/element.svg") 50% 100% no-repeat,
    url("../img/element2.svg") 5% 60% no-repeat,
    url("../img/element3.svg") 40% 30% no-repeat;
  height: 758px;
  margin-bottom: 270px;
}

.navigation,
.navigation-link-items,
.navigation-link-items,
.navigation-top-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation {
  margin-bottom: 114px;
}

.navigation-link-items {
  list-style: none;
}

.navigation-link {
  font-weight: 300;
  padding-right: 35px;
  text-decoration: none;
  font-size: 16px;
  line-height: 19px;
  color: #B8BECD;
}

.navigation-link-items .active {
  font-weight: 500;
  color: #202336;
}

.login,
.register,
.form-options button,
.main-button,
.guides-button {
  border: 1px solid #FB8F1D;
  border-radius: 8px;
  font-weight: 500;
  font-size: 15px;
  color: #FB8F1D;
  padding: 12px 46px;
  background-color: #FFFFFF;
  cursor: pointer;
}

.register {
  padding: 12px 36px;
  background: #FB8F1D;
  color: #FFFFFF;
}

.title {
  font-family: Playfair Display, sans-serif;
  font-weight: bold;
  font-size: 64px;
  line-height: 80px;
  color: #202336;
}

.subtitle {
  font-weight: 600;
  font-size: 22px;
  line-height: 27px;
  color: #202336;
  display: flex;
  flex-direction: column;
}

.subtitle:after {
  content: '';
  border: black 1px solid;
  width: 32px;
  background: #202336;
  border-radius: 4px;
  margin-top: 21px;
  margin-bottom: 48px;
}

.wrapper {
  width: 48%;
}

.form-options {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-basis: 35%;
}

.form-options select {
  width: 245px;
  padding: 21px 129px 21px 31px;
  border: 0.8px solid #AFB0B9;
  box-sizing: border-box;
  border-radius: 6px;
  margin-bottom: 35px;
  margin-right: 30px;
  margin-left: 0;
}

.form-options button {
  padding: 16px 60px;
  background: #FB8F1D;
  color: #FFFFFF;
  font-size: 17px;
}

.main {
  background: url("../img/main-bg.png") 10% 50% no-repeat,
    url("../img/element.svg") 80% 70% no-repeat,
    url("../img/element3.svg") 50% 10% no-repeat;
  margin-bottom: 262px;
}

.main-wrapper {
  display: flex;
  justify-content: flex-end;
}

.main-description {
  width: 40%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.main-title {
  font-family: Playfair Display, sans-serif;
  font-weight: bold;
  font-size: 36px;
  line-height: 50px;
  color: #000000;
  margin-right: 27px;
  margin-bottom: 20px;
}

.main-description-text {
  font-weight: 300;
  font-size: 17px;
  line-height: 30px;
  color: #7D7987;
  margin-bottom: 33px;
}

.main-button {
  padding: 16px 60px;
  color: #FFFFFF;
  font-size: 17px;
  background: #FB8F1D;
  border-radius: 8px;
}

.destinations {
  background: url("../img/element.svg") 40% 98% no-repeat,
    url("../img/element2.svg") 5% 60% no-repeat,
    url("../img/element3.svg") 60% 3% no-repeat;
  margin-bottom: 232px;
}

.destination-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-decoration: none;
  padding-bottom: 56px;
}

.destinations-title {
  font-family: Playfair Display, sans-serif;
  font-weight: bold;
  font-size: 36px;
  line-height: 50px;
  color: #000000;
}

.link-view-all {
  text-decoration: none;
  font-weight: 600;
  font-size: 17px;
  line-height: 60px;
  color: #FB8F1D;
  padding-right: 20px;
}

.link-view-all:after {
  content: '';
  background: url('../img/viaw-all.svg') 60% 60% no-repeat;
  margin-left: 11px;
  padding-right: 20px;
}

.destinations-content {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.destinations-source {
  width: 120px;
  background-color: #fff;
  display: inline-block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-radius: 0 10px 0 0;
  padding: 11px 20px 5px 0;
}

.destinations-country {
  font-weight: 500;
  font-size: 15px;
  line-height: 24px;
  color: #80918E;
}

.destinations-city {
  font-weight: 600;
  font-size: 17px;
  line-height: 25px;
  color: #042521;
  display: block;
}

.destinations-wrapper {
  position: relative;
  width: 261px;
  height: 337px;
  overflow: hidden;
  border-radius: 8px;
  margin-bottom: 15px;
}

.destinations-img {
  height: 100%;
}

.destinations-img.china {
  position: absolute;
  left: -90px;
  bottom: 0;
}

.guides {
  background: url("../img/guides-bg.png") 85% no-repeat,
    url("../img/element.svg") 4% 100% no-repeat,
    url("../img/element2.svg") 50% 60% no-repeat,
    url("../img/element3.svg") 30% 30% no-repeat;
  align-items: center;
  padding-top: 80px;
  height: 680px;
  margin-bottom: 248px;
}

.guides-wrapper {
  display: flex;
}

.guides-description {
  margin: 0;
  margin-left: 26px;
  align-items: center;
  flex-basis: 40%;
}

.guides-title {
  font-family: Playfair Display, sans-serif;
  font-weight: bold;
  font-size: 36px;
  line-height: 50px;
  color: #000000;
}

.guides-description-text {
  font-weight: 300;
  font-size: 17px;
  line-height: 30px;
  color: #7D7987;
  padding-bottom: 39px;
}

.guides-button {
  padding: 16px 60px;
  color: #FFFFFF;
  font-size: 17px;
  background: #FB8F1D;
  border-radius: 8px;
}

.feedback {
  background: url("../img/element.svg") 15% 100% no-repeat,
    url("../img/element2.svg") 60% 60% no-repeat,
    url("../img/element3.svg") 50% 30% no-repeat;
  margin-bottom: 213px;
}

.feedback-title {
  font-family: Playfair Display, sans-serif;
  font-weight: bold;
  font-size: 36px;
  line-height: 50px;
  color: #000000;
  margin-left: 36px;
}

.feedback-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.feedback-content {
  flex-basis: 40%;
  margin-left: 36px;
}

.feedback-content-img {
  position: relative;
  width: 384px;
  height: 491px;
  overflow: hidden;
  border-radius: 16px 16px 0 16px;
}

.feedback-buttons {
  width: 132px;
  height: 60px;
  background-color: #FFFFFF;
  display: inline-block;
  position: absolute;
  right: 0;
  bottom: 0;
  border-radius: 16px 0 0 0;
}

.feedback-img {
  height: 100%;
}

.feedback-text {
  font-size: 24px;
  line-height: 36px;
  color: #000000;
  margin-bottom: 40px;
}

.feedback-person {
  display: block;
  font-weight: bold;
  font-size: 22px;
  line-height: 48px;
  color: #000000;
}

.feedback-position {
  font-size: 18px;
  line-height: 48px;
  color: rgba(0, 0, 0, 0.85);
}

.feedback-star {
  margin-right: 15px;
}

.footer {
  padding-top: 79px;
}

.footer-wrapper {
  display: flex;
  justify-content: space-between;
  border-bottom: #AFB0B9 1px solid;
  padding-bottom: 70px;
}

.footer-logo {
  flex-basis: 30%;
}

.footer_list_item {
  list-style: none;
  font-weight: 300;
  font-size: 18px;
  line-height: 38px;
}

.footer_list_item a {
  text-decoration: none;
  color: #848484;
}

.footer-list {
  padding: 0;
  margin: 0;
}

.footer-link-title {
  font-weight: bold;
  font-size: 19px;
  line-height: 60px;
  color: #000000;
}

.social-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 23px;
}

.social-wrapper img {
  padding-right: 35px;
}

.footer-logo {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.footer-logo-img {
  padding-bottom: 40px;
}

.footer-text {
  font-weight: 300;
  font-size: 18px;
  line-height: 28px;
  color: #848484;
  padding-bottom: 55px;
}

.footer-copyright {
  font-weight: 300;
  font-size: 17px;
  line-height: 28px;
  color: #848484;
}

.header-stories {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-stories-title {
  font-weight: bold;
  font-size: 36px;
  line-height: 50px;
  color: #000000;
}

.stories {
  background: url("../img/element.svg") 38% 100% no-repeat,
    url("../img/element2.svg") 5% 60% no-repeat,
    url("../img/element3.svg") 55% 3% no-repeat;
  margin-bottom: 195px;
}

.stories-wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: stretch;
}

.stories-content {
  width: 266px;
}

.stories-content-img {
  max-width: 120%;
  height: auto;
}

.stories-content-img-wrapper {
  border-radius: 15px 15px 0 0;
  height: 13em;
  overflow: hidden;
}

.stories-content-title {
  font-weight: bold;
  font-size: 19px;
  line-height: 30px;
  color: #000000;
}

.stories-content-text {
  font-size: 15px;
  line-height: 26px;
  color: #7D7987;
}

.stories-content-link {
  font-weight: 600;
  font-size: 15px;
  line-height: 28px;
  color: #FB8F1D;
  display: block;
  text-decoration: none;
}

.m-menu-button {
  padding: 5px;
  background-color: #fff;
  border: none;
  cursor: pointer;
}

.menu-line {
  width: 20px;
  display: block;
  height: 3px;
  background-color: #000000;
  margin-top: 3px;
  margin-bottom: 3px;
  border-radius: 40px;
}

.mobile-buttons {
  display: none;
}

.no-scroll {
  overflow: hidden;
}

.feedback-buttons {
  padding-top: 15px;
  padding-left: 15px;
}

.button-next {
  cursor: pointer;
  margin-left: 20px;
  border: none;
  width: 40px;
  height: 40px;
  background: url('../img/buttons/next.svg') no-repeat;
  background-position: center;
}

.button-prev {
  border: none;
  cursor: pointer;
  width: 40px;
  height: 40px;
  background: url('../img/buttons/next.svg') no-repeat;
  transform: rotate(180deg);
  background-position: center;
}

// @media
/* Large desktops and laptops */
@media (max-width: 1200px) {
  .container {
    width: 95%;
  }
}

/* Landscape phones and portrait tablets */
@media (max-width: 900px) {
  .container {
    width: 95%;
  }

  .wrapper {
    width: 80%;
  }

  .header {
    background: url("../img/hero-bg.png") 85% 100% no-repeat;
    background-size: 80%;
  }

  .login,
  .register,
  .form-options button,
  .main-button,
  .guides-button {
    font-size: 13px;
    padding: 8px 35px;
  }

  .register {
    padding: 8px 35px;
  }

  .form-options button {
    padding: 14px 60px;
    font-size: 14px;
  }

  .title {
    font-size: 40px;
    line-height: 80px;
    color: #202336;
  }

  .navigation {
    margin-bottom: 50px;
  }

  .header {
    margin-bottom: 50px;
  }

  .main {
    background: url("../img/main-bg.png") 10% 50% no-repeat;
    margin-bottom: 10px;
    background-size: 45%;
    height: auto;
  }

  .main-description {
    width: 50%;
  }

  .destinations-wrapper {
    margin-bottom: 15px;
  }

  .guides {
    background: url("../img/guides-bg.png") 85% no-repeat;
    background-size: 50%;
    padding-top: 80px;
    margin-bottom: 50px;
    height: auto;
  }

  .guides-title {
    font-size: 28px;
    line-height: 50px;
    color: #000000;
  }

  .feedback-wrapper {
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }

  .feedback-content {
    flex-basis: 100%;
  }

  .stories-wrapper {
    justify-content: space-evenly;
  }

  .footer, .guides {
    padding-top: 0;
  }

  .header-stories-title, .feedback-title {
    font-size: 28px;
    padding: 0;
    margin-bottom: 5px;
  }

  .feedback-text {
    font-size: 18px;
    margin-bottom: 5px;
  }

  
}

@media (max-width: 780px) {
  .navigation-top-nav {
    display: none;
  }

  .m-menu {
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    position: fixed;
    left: 0;
    top: 100px;
    right: 0;
    bottom: 0;
    background: #1abe84;
    height: calc(100% - 68px);
  }

  .mobile-buttons {
    display: block;
    margin-left: auto;
  }

  .m-menu.active {
    display: flex;
  }

  .navigation-top-nav {
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
  }

  .navigation-link-items {
    display: flex;
    flex-direction: column;
    padding-left: 0;
    padding-right: 0;
    align-items:center;
    justify-content: center;
  }

  .navigation-link {
    display: flex;
    margin-bottom: 25px;
    padding-right: 0;
    color: #ffb566;
    font-weight: bold;
    align-items:center;
    justify-content: center;
  }

  .button-group {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .login {
    margin-bottom: 10%;
  }

  .footer-wrapper {
    flex-wrap: wrap;
  }

  .footer-logo {
    margin-top: 15px;
    flex-basis: 100%;
    order: 1;
  }
  
  .form-options select {
    flex-wrap: wrap;
    
  }

  .footer_list_item {
    font-size: 15px;
  }

  .main-description {
    padding-top: 25px;
    flex-basis: 60%;
  }

}

/* Portrait phones and smaller */
@media (max-width: 480px) {}

/* Iphone 5 */
@media (max-width: 320px) {}